<template>
  <div ref="chart" style="width: 434px;height:206px">
  </div>
</template>

<script>
import '@/utils/tagcanvas.js';

export default {
  data: function () {
    return {
      myChart: null,
    };
  },
  props: {
    dataX: {
      type: Array,
      default: () => [],
    },
    dataY: {
      type: Array,
      default: () => [],
    },
    colorStart: {
      type: String,
      default: () => 'rgba(7,172,215,1)',
    },
    colorEnd: {
      type: String,
      default: () => 'rgba(7,172,215,0.6)',
    },
  },
  methods: {
    getEchartData() {
      const chart = this.$refs.chart
      if (this.myChart != null && this.myChart != '' && this.myChart != undefined) {
        this.myChart.dispose(); //
      }
      if (chart) {
        this.myChart = this.$echarts.init(chart)
        const option = {
          tooltip: {
            // trigger: 'axis',
            // axisPointer: {
            //   type: 'shadow'
            // },
            backgroundColor: 'rgba(9, 24, 48, 0.5)',
            // borderColor: 'rgba(75, 253, 238, 0.4)',
            textStyle: {
              color: '#CFE3FC',
            },
            borderWidth: 1,
          },
          grid: {
            top: '15%',
            right: '5%',
            left: "5%",
            bottom: '12%'
          },
          xAxis: {
            type: 'category',
            data: this.dataX,
            axisLine: {
              lineStyle: {
                color: 'rgba(216,216,216,0.1)'
              }
            },
            axisTick: {
              show: false,
            },
            axisLabel: {
              color: '#FFFFFF',
              fontWeight: 500,
              fontFamily: 'PingFangSC-Medium',
              fontSize: 14,
            }
          },
          yAxis: {
            type: 'value',
            splitLine: {
              lineStyle: {
                color: 'rgba(216,216,216,0.1)'
              }
            },
            axisLine: {
              lineStyle: {
                color: 'rgba(216,216,216,0.1)'
              }
            },
            axisTick: {
              show: false,
            },
            axisLabel: {
              show: false,
            },
          },
          series: [
            {
              data: this.dataY,
              type: 'bar',
              barWidth: '30%',
              itemStyle: {
                color: {
                  type: 'linear',
                  x: 0,
                  y: 0,
                  x2: 0,
                  y2: 1,
                  colorStops: [{
                      offset: 0, color: this.colorStart // 0% 
                  }, {
                      offset: 1, color: this.colorEnd // 100% 
                  }],
                  global: false //  false
                },
              // shadowColor: 'rgba(0,160,221,1)',
              // shadowBlur: 4,
              },
            }
          ]
        };
        this.myChart.setOption(option)
        // window.addEventListener("resize", function() {
        //   this.myChart.resize()
        // })
      }
      // this.$on('hook:destroyed',()=>{
      //   window.removeEventListener("resize", function() {
      //     this.myChart.resize();
      //   });
      // })
    },
  },
  watch: {
    //   tagcanvas
    dataX: {
      handler (newName, oldName) {
        this.getEchartData();
      },
      immediate: true,
      deep: true
    },
    dataY: {
      handler (newName, oldName) {
        this.getEchartData();
      },
      immediate: true,
      deep: true
    },
    colorStart: {
      handler (newName, oldName) {
        this.getEchartData();
      },
      immediate: true,
      deep: true
    },
    colorEnd: {
      handler (newName, oldName) {
        this.getEchartData();
      },
      immediate: true,
      deep: true
    }
  },
  mounted() {
    this.getEchartData()
  },
};
</script>
 
