          const reverseNumber = (value + '').split('').reverse()
          let valueStr = ''
          while (reverseNumber.length) {
            const seg = reverseNumber.splice(0, 3).join('')
            valueStr += seg
            if (seg.length === 3) valueStr += ','
          }
          return valueStr.split('').reverse().join('')
        }
      },
      soldSum:{
