<template>
  <div class="world-cloud-3d">
    <div class="world-cloud-canvas-wrapper">
      <canvas
        :id="'world-cloud-canvas'+type"
        width="400"
        height="200"
        style="width: 100%; max-width: 400px"
      >
      </canvas>
    </div>
    <div style="display: none" id="weightTags"></div>
  </div>
</template>

<script>
import '@/utils/tagcanvas.js';

export default {
  data: function () {
    return {
    };
  },
  props: {
    wordArr: {
      type: Array,
      default: () => [],
    },
    type: {
      type: String,
      default: () => '',
    }
  },
  methods: {
    // 
    startWorldCloud: function (updateFlag) {
      this.createTagListDom();
      let o = {
        maxSpeed: 0.01, // 
        minSpeed: 0.01, // 
        textHeight: 25,
        outlineMethod: "colour", // tag hover  
        fadeIn: 800,
        outlineColour: "rgba(7,172,215,1)",
        outlineOffset: 0,
        depth: 0.97,
        minBrightness: 1,
        wheelZoom: false,
        reverse: true, // 
        shadowBlur: 1,
        shuffleTags: true,
        shadowOffset: [1, 1],
        stretchX: 1.7, // Stretch or compress the cloud horizontally. 
        initial: [0.1, 0.1], // 
        textFont: null, //  null   taga 
        textColour: null, //  null   taga 
        weight: true, // weight tag 
        weightMode: "size", // 
        weightSize: 1, //  tag  
        clickToFront: 800,
      };
      try {
        //  tagcanvas, 
        if (!updateFlag) {
          TagCanvas.Start(`world-cloud-canvas${this.type}`, "weightTags", o);
        } else {
          TagCanvas.Update(`world-cloud-canvas${this.type}`);
        }
      } catch (e) {}
    },
    //  wordArr  TagList Domcanvas 
    //  { name: tag light: true/false }
    createTagListDom: function () {
      let res = [...this.wordArr];
      let fragment = new DocumentFragment();
      for (let i = 0; i < res.length; i++) {
        let a = document.createElement("a");
        // 10
        if (res[i].name.length > 10) {
          let charArr = res[i].name.split("");
          charArr.splice(10, 0, "<br>");
          res[i].name = charArr.join("");
        }
        a.innerHTML = res[i].name;
        a.href = "javascript:void(0)";
        //   huge large,  medium small
        if (res[i].light) {
          let readomValue = Math.random();
          a.className = this.type == 'topBrand' ? "huge" : "large";
        } else {
          let readomValue = Math.random();
          a.className = readomValue > 0.5 ? "medium" : "small";
        }
        fragment.append(a);
      }
      //  tagContainer tag
      let tagsContainer = document.querySelector("#weightTags");
      tagsContainer.innerHTML = "";
      tagsContainer.append(fragment);
    },
  },
  watch: {
    //   tagcanvas
    wordArr: function () {
      this.startWorldCloud(true);
    },
  },
  mounted() {
    //   tagcanvas
    this.startWorldCloud();
  },
};
</script>
 
<style lang="scss">
.world-cloud-3d {
  .world-cloud-canvas-wrapper {
    width: 400px;
    height: 200px;
    max-width: 400px;
    max-height: 200px;
  }
  #weightTags {
    font-size: 12px;
    .huge {
      font-size: 35px;
      color: #FDC830;
    }
    .large {
      font-size: 35px;
      color: #1FD8C3;
    }
    .medium {
      font-size: 25px;
      color: rgba(255,255,255,0.8);
    }
    .small {
      font-size: 20px;
      // color: #8cd192aa;
      color: rgba(255,255,255,0.3);
    }
  }
}
</style>
