<template>
  <div ref="chart" style="width: 434px;height:206px">
  </div>
</template>

<script>
import '@/utils/tagcanvas.js';

export default {
  data: function () {
    return {
      myChart: null,
    };
  },
  props: {
    chartData: {
      type: Array,
      default: () => [],
    },
  },
  methods: {
    getEchartData() {
      const chart = this.$refs.chart
      if (this.myChart != null && this.myChart != '' && this.myChart != undefined) {
        this.myChart.dispose(); //
      }
      if (chart) {
        this.myChart = this.$echarts.init(chart)
        let sum = 0
        if(this.chartData.length){
          this.chartData.map(item => {
            sum= sum + item.value
          })
        }
        let data = this.chartData
        // 
        const option = {
          // color: ['rgba(7,172,215,1)', 'rgba(31,216,195,1)', 'rgba(7,215,149,1)', 'rgba(244,140,37,1)', 'rgba(253,200,48,1)', 'rgba(42,122,255,1)'],
          tooltip: {
            backgroundColor: 'rgba(9, 24, 48, 0.5)',
            // borderColor: 'rgba(75, 253, 238, 0.4)',
            textStyle: {
              color: '#CFE3FC',
            },
            borderWidth: 1,
            confine: true,
          },
          legend: [
            {
              data: ["XXXX4", "XXXX5", "XXXX6"],
              selectedMode: false,
              orient: 'vertical',
              right: '2%',
              top: 'center',
              itemWidth: 6,
              itemHeight: 30,
              padding: 10,
              itemGap: 25,
              formatter(name) { // 
                let total = 0
                let value = 0
                if(data.length){
                  data.forEach((item) => {
                    total += item.value
                    if (item.name == name) {
                      value = item.value
                    }
                  })
                }
                return ['{a|' + `${name}` + '}\n{b|' + `${value}` + '}']
              },
              textStyle: {
                fontSize: 24,
                color: '#A8CCDE',
                padding: [10, 0],
                rich: {
                  a: {
                    fontSize: 14,
                    fontWeight: 500,
                    color: "rgba(255,255,255,0.6)",
                  },
                  b: {
                    fontSize: 18,
                    fontFamily: "YouSheBiaoTiHei",
                    fontWeight: 400,
                    color: "#FFFFFF",
                    textShadowColor: '#2F84F3',
                    textShadowBlur: 4,
                    textShadowOffsetY: 2,
                  },
                }
              }
            },
            {
              data: ["XXXX1", "XXXX2", "XXXX3"],
              selectedMode: false,
              orient: 'vertical',
              right: '30%',
              top: 'center',
              itemWidth: 6,
              itemHeight: 30,
              padding: 10,
              itemGap: 25,
              formatter(name) { // 
                let total = 0
                let value = 0
                if(data.length){
                  data.forEach((item) => {
                    total += item.value
                    if (item.name == name) {
                      value = item.value
                    }
                  })
                }
                return ['{a|' + `${name}` + '}\n{b|' + `${value}` + '}']
              },
              textStyle: {
                fontSize: 24,
                color: '#A8CCDE',
                padding: [2, 0],
                rich: {
                  a: {
                    fontSize: 14,
                    fontWeight: 500,
                    color: "rgba(255,255,255,0.6)",
                  },
                  b: {
                    fontSize: 18,
                    fontFamily: "YouSheBiaoTiHei",
                    fontWeight: 400,
                    color: "#FFFFFF",
                    textShadowColor: '#2F84F3',
                    textShadowBlur: 4,
                    textShadowOffsetY: 2,
                  },
                }
              }
            }
          ],
          title: {
            left: "23%",
            top: "38%",
            textAlign: "center",
            text: "{a|" + sum + "}",
            textStyle: {
              fontSize: 14,
              rich: {
                a: {
                  fontSize: 22,
                  fontFamily: "YouSheBiaoTiHei",
                  fontWeight: 400,
                  color: "#FFFFFF",
                  textShadowColor: '#2F84F3',
                  textShadowBlur: 4,
                  textShadowOffsetY: 2,
                }
              },
            },
            subtext: "SUM",
            subtextStyle: {
              fontSize: 10,
              color: "#FFFFFF",
              fontWeight: 500,
            },
          },
          graphic: {
            elements: [{
              type: 'rect',
              rotation: 0.8,
              left: '11.5%',
              top: 'middle',
              shape: {
                // x: api.getWidth()*0.24/2,
                // y: api.getHeight()*0.5/2,
                width: 76,
                height:76,
              },
              style: {
                stroke: 'rgba(42,122,255,1)',
                fill: "rgba(42,122,255,0.10)",
                lineWidth: 1
              },
            }],
          },
          series: [
            {
              name: '',
              type: 'pie',
              radius: ['85%', '100%'],
              center: ['24%', '50%'],
              hoverAnimation: false,
              avoidLabelOverlap: false,
              itemStyle: {
                // borderRadius: 10,
                borderColor: 'rgb(8 18 31)',
                borderWidth: 8,
                color: (params) => {
                  if(params.data.name == 'XXXX1'){
                    return 'rgba(42,122,255,1)'
                  }
                  else if(params.data.name == 'XXXX2'){
                    return 'rgba(31,216,195,1)'
                  }
                  else if(params.data.name == 'XXXX3'){
                    return 'rgba(244,140,37,1)'
                  }
                  else if(params.data.name == 'XXXX4'){
                    return 'rgba(7,172,215,1)'
                  }
                  else if(params.data.name == 'XXXX5'){
                    return 'rgba(7,215,149,1)'
                  }
                  else if(params.data.name == 'XXXX6'){
                    return 'rgba(253,200,48,1)'
                  }
                },
              },
              label: {
                show: false,
                position: 'center',
              },
              labelLine: {
                show: false,
              },
              data: this.chartData,
            },
            {
              name: '',
              type: 'pie',
              radius: ['55%', '70%'],
              center: ['24%', '50%'],
              hoverAnimation: false,
              avoidLabelOverlap: false,
              itemStyle: {
                // borderRadius: 10,
                color: 'rgba(196, 196, 196, 0.1)',
                borderColor: 'rgb(8 18 31)',
                borderWidth: 6,
              },
              label: {
                show: false,
                position: 'center',
              },
              labelLine: {
                show: false,
              },
              tooltip: {
                show: false,
              },
              data: this.chartData,
            }
          ],
        };
        this.myChart.setOption(option)
        // window.addEventListener("resize", function() {
        //   this.myChart.resize()
        // })
      }
      // this.$on('hook:destroyed',()=>{
      //   window.removeEventListener("resize", function() {
      //     this.myChart.resize();
      //   });
      // })
    },
  },
  watch: {
    chartData: {
      handler (newName, oldName) {
        this.getEchartData();
      },
      immediate: true,
      deep: true
    },
  },
  mounted() {
    this.getEchartData()
  },
};
</script>
 
